/* eslint-disable eqeqeq */
import React, { useState, useEffect } from "react";
// import fetch from "node-fetch";
import {data as json} from "./data.json";
import "./App.css";

export default function App(){

  const [data, setData] = useState([]);
  const [search, setSearch] = useState("");
  const [length, setLength] = useState(0);

  // setData(json);

  useEffect(() => {
    // fetch(`http://192.168.1.7:8080`).then(res => res.json()).then(res => {
    //   console.log(res);
    //   setData(res.data);
    //   setLength(res.data.length);
    // }).catch(console.error);
    setData(json);
    setLength(json.length);
  }, []);

  return (
    <>
      <div className="container">
        <h4>เฉลย Virtual School ม.6</h4>
        <input type="text" id="key" className="form-control" onChange={(e) => {
          setSearch(e.target.value.toLowerCase());
          setLength(data.filter(value => {
            if(e.target.value == "") return value;
            if(value.key.toLowerCase().includes(e.target.value.toLowerCase())){
              return value;
            }
          }).length);
        }} placeholder="Key..." />
        <span>All result {length} data</span>
        <table className="table table-striped table-hover table-bordered table-dark">
          <thead>
            <tr>
              <td>Key</td>
              <td>Value</td>
            </tr>
          </thead>
          <tbody>
            {
              data.filter((value, i) => {
                if(search == "") return value;
                if(value.key.toLowerCase().includes(search)){
                  return value;
                }
              }).map((res) => {
                return <tr><td>{res.key}</td><td>{res.value}</td></tr>
              })
            }
            { !length ? <tr><td colSpan={2}>undefined</td></tr> : undefined }
          </tbody>
        </table>
      </div>
    </>
  )
}